.App {
  font-family: 'Roboto';
  display: flex;
  flex-direction: column;
  height: fit-content;
  text-align: center;
}

.header {
  position: relative;
  margin: 0;
  box-sizing: border-box;
  box-shadow: 0px 3px 3px 0 rgba(0, 0, 0, 0.4);
}
.header::after {
  content: "";
  position: absolute;
  top: 0px;
  left: -100px;
  width: 0;
  height: 0;
  box-sizing: border-box;
  border: 60px solid black;
  border-color: transparent transparent #96bf48 #96bf48;
  transform-origin: 0 0;
  transform: rotate(-45deg);
  box-shadow: 3px 3px 3px 0px rgba(0, 0, 0, 0.4);
  transition: ease 0.3s;
}
.header:hover::after {
  content: "";
  position: absolute;
  top: 0px;
  left: -120px;
  width: 0;
  height: 0;
  box-sizing: border-box;
  border: 80px solid black;
  border-color: transparent transparent #96bf48 #96bf48;
  transform-origin: 0 0;
  transform: rotate(-45deg);
  box-shadow: 3px 3px 3px 0px rgba(0, 0, 0, 0.4);
}

.github-link{
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 99;
  animation: ease 0.8s;
}

.page{
  display: flex;
  flex-direction: column;
  width: 50%;
  gap: 2vh;
  justify-content: center;
  text-align: center;
  margin: auto;
  padding-top: 20vh;
  padding-bottom: 5vh;
}

.send{
  text-align: right;
}

.responses{
  display: flex;
  flex-direction: column-reverse;
  gap: 2vh;
  height: fit-content;
  margin: auto;
  justify-content: center;
  text-align: left;
  flex-wrap: wrap;
  width: 70%;
  position: relative;
  transition: ease 0.3s;
  padding-bottom: 10vh;
}

.cardTransition{  
  transition: ease 0.6s;
  animation: fadeIn 0.6s;
  -webkit-animation: fadeIn 0.6s;
  -moz-animation: fadeIn 0.6s;
  -o-animation: fadeIn 0.6s;
  -ms-animation: fadeIn 0.6s;
}

@keyframes fadeIn {
  0% {
    transform: translateX(-100vh);
  }
  100% {
    transform: translateX(0px);
  }
}

@-moz-keyframes fadeIn {
  0% {
    transform: translateX(-100vh);
  }
  100% {
    transform: translateX(0px);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    transform: translateX(-100vh);
  }
  100% {
    transform: translateX(0px);
  }
}

@-o-keyframes fadeIn {
  0% {
    transform: translateX(-100vh);
  }
  100% {
    transform: translateX(0px);
  }
}

@-ms-keyframes fadeIn {
  0% {
    transform: translateX(-100vh);
  }
  100% {
    transform: translateX(0px);
  }
}